/* MS supports */
.ps {
	overflow: hidden !important;
	overflow-anchor: none;
	-ms-overflow-style: none;
	touch-action: auto;
	-ms-touch-action: auto;
	&:hover {
		>.ps__rail-x {
			opacity: 0.6;
		}
		>.ps__rail-y {
			opacity: 0.6;
		}
	}
	.ps__rail-x {
		&:hover {
			background-color: #eee;
			opacity: 0.9;
		}
		&:focus {
			background-color: #eee;
			opacity: 0.9;
		}
	}
	.ps__rail-y {
		&:hover {
			background-color: #eee;
			opacity: 0.9;
		}
		&:focus {
			background-color: #eee;
			opacity: 0.9;
		}
	}
	.ps__rail-x.ps--clicking {
		background-color: #eee;
		opacity: 0.9;
	}
	.ps__rail-y.ps--clicking {
		background-color: #eee;
		opacity: 0.9;
	}
}
.ps__rail-x {
	display: none;
	opacity: 0;
	transition: background-color .2s linear, opacity .2s linear;
	-webkit-transition: background-color .2s linear, opacity .2s linear;
	height: 2px;
	bottom: 0px;
	position: absolute;
	&:hover {
		>.ps__thumb-x {
			background-color: #999;
			height: 11px;
		}
	}
	&:focus {
		>.ps__thumb-x {
			background-color: #999;
			height: 11px;
		}
	}
}
.ps__rail-y {
	display: none;
	opacity: 0;
	transition: background-color .2s linear, opacity .2s linear;
	-webkit-transition: background-color .2s linear, opacity .2s linear;
	width: 2px;
	right: 0;
	position: absolute;
	&:hover {
		>.ps__thumb-y {
			background-color: #999;
			width: 2px;
		}
	}
	&:focus {
		>.ps__thumb-y {
			background-color: #999;
			width: 2px;
		}
	}
}
.ps--active-x {
	>.ps__rail-x {
		display: block;
		background-color: transparent;
	}
}
.ps--active-y {
	>.ps__rail-y {
		display: block;
		background-color: transparent;
	}
}
.ps--focus {
	>.ps__rail-x {
		opacity: 0.6;
	}
	>.ps__rail-y {
		opacity: 0.6;
	}
}
.ps--scrolling-x {
	>.ps__rail-x {
		opacity: 0.6;
	}
}
.ps--scrolling-y {
	>.ps__rail-y {
		opacity: 0.6;
	}
}
.ps__thumb-x {
	background-color: #aaa;
	border-radius: 6px;
	transition: background-color .2s linear, height .2s ease-in-out;
	-webkit-transition: background-color .2s linear, height .2s ease-in-out;
	height: 6px;
	bottom: 2px;
	position: absolute;
}
.ps__thumb-y {
	background-color: rgba(255,255,255,0.2);
	border-radius: 6px;
	transition: background-color .2s linear, width .2s ease-in-out;
	-webkit-transition: background-color .2s linear, width .2s ease-in-out;
	width: 2px;
	right: 2px;
	position: absolute;
}
.ps__rail-x.ps--clicking {
	.ps__thumb-x {
		background-color: #999;
		height: 11px;
	}
}
.ps__rail-y.ps--clicking {
	.ps__thumb-y {
		background-color: #999;
		width: 2px;
	}
}
@media screen and (-ms-high-contrast: active) {
	.ps {
		overflow: auto !important;
	}
}
@media (-ms-high-contrast: none) {
	.ps {
		overflow: auto !important;
	}
}
